@import url(https://fonts.googleapis.com/css?family=Baloo+Chettan+2&display=swap);@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono&display=swap);
/* ------------------------------------ */
* {
  font-family: 'Ubuntu mono', monospace;
	margin: 0px; 
	padding: 0px; 
	-webkit-box-sizing: border-box; 
	        box-sizing: border-box;
}
body, html {
    background: #fafafa;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.stillage-table {
    border-collapse: collapse;
    background: white;
    width: 100%;
    margin: 0 auto;
    position: relative;
    table-layout: fixed;
    border: none;
    font-size: small;
}
.stillage-table__row {
    height: 5em;
}
.stillage-table__cell {
    border: 1px solid #ccc;
    padding-top: 16px;
    padding-bottom: 16px;
    -webkit-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
    overflow: hidden;
    cursor: default;
}
.stillage-table__cell:hover {
    font-size: 110%;
}
.stillage-table__cell--barcode:hover {
    background: #d8fff7;
}
.stillage-table__cell--selected {
    background: #00ffcc;
    color: #22373f;
    font-weight: bold;
}
.stillage-table__cell--selected:hover {
    background: #00ffcc;
}
.stillage-table__cell--empty {
    color: #ff3300;
}
.stillage-table__cell--empty:hover {
    background: #ffefeb;
}
.stillage-table__cell--na {
    color: #999;
    background: #fff;
}
.stillage-table__cell--na:hover {
    background: #e5eef1;
}
.stillage-table__row-index {
    text-align: right;
    padding-right: 1em;
}
.stillage-table__col-index {
    text-align: center;
    padding-top: 1em;
}
.stillage-table__title {
    color: #22373f;
    margin-bottom: .4em;
}
.stillage-table__subtitle {
    color: #666;
    margin-bottom: 1em;
}


.timestamp-visualizer__current-frame {
    width: 7em;
}
.timestamp-visualizer__frame-range {
    width: calc(89% - 8em);
}
.timestamp-visualizer__frame-range {
  margin: 10px 0;
  background-color: transparent;
  -webkit-appearance: none;
}
input[type=range]:focus {
    outline: 0px;
  -webkit-box-shadow: 0 0px 6px rgba(0, 255, 204, 0.5), 0 0px 6px rgba(0, 255, 204, 0.5);
          box-shadow: 0 0px 6px rgba(0, 255, 204, 0.5), 0 0px 6px rgba(0, 255, 204, 0.5);
}
input[type=range]::-webkit-slider-runnable-track {
  background: #22373f;
  border: none;
  border-radius: 3px;
  width: 100%;
  height: 6px;
  cursor: pointer;
}
input[type=range]::-webkit-slider-thumb {
  margin-top: -11px;
  width: 30px;
  height: 30px;
  background: #00ffcc;
  border: 1px solid #ffffff;
  border-radius: 15px;
  cursor: pointer;
  -webkit-appearance: none;
}
input[type=range]::-moz-range-thumb {
  width: 30px;
  height: 30px;
  background: #00ffcc;
  border: 1px solid #ffffff;
  border-radius: 15px;
  cursor: pointer;
}
input[type=range]::-ms-thumb {
  width: 30px;
  height: 30px;
  background: #00ffcc;
  border: 1px solid #ffffff;
  border-radius: 15px;
  cursor: pointer;
  margin-top: 0px;
  /*Needed to keep the Edge thumb centred*/
}
input[type=range]:focus::-ms-fill-lower {
  background: #424242;
}
input[type=range]:focus::-ms-fill-upper {
  background: #4f4f4f;
}



h1[data-v-d8ec41bc], h2[data-v-d8ec41bc] {
  font-weight: normal;
}
ul[data-v-d8ec41bc] {
  list-style-type: none;
  padding: 0;
}
li[data-v-d8ec41bc] {
  display: inline-block;
  margin: 0 10px;
}
a[data-v-d8ec41bc] {
  color: #42b983;
}
